.add-thread-wrapper{
    background-color: white;
    -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,0.50);
            box-shadow: 0 1px 4px 0 rgba(0,0,0,0.50);
    min-height: 400px;
    border-radius: 3px;
    -ms-flex-line-pack: center;
        align-content: center;
    padding: 25px 25px;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
}
.comment-post-wrapper{
    padding: 25px 25px;
    background: #F4F4F4;
    -webkit-box-shadow: 0 1px 1px 0 rgba(0,0,0,0.50);
            box-shadow: 0 1px 1px 0 rgba(0,0,0,0.50);
    border-radius: 5px;
}

.subforumpath-wrapper{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.subforumpath-wrapper div{
    display: -ms-flexbox;
    display: flex;
    color: rgba(0, 0, 0, 0.40);
    height: 100%;
    line-height: 1.1875em;
}
.subforumpath-wrapper .subforum-link{
    text-align: right;
    padding: 0 10px;
}


